@font-face {
  font-family: RobotoCondensed;
  src: url(/MimoPortfolio/static/media/roboto-condensed.light.cf4449c1.ttf);
  font-weight: 800;
}

@font-face {
  font-family: EconomicaBold;
  src: url(/MimoPortfolio/static/media/economica-bold.82620571.ttf);
}

body {
  text-align: center;
  font-size: 20px;
  padding: 2.5%;

  font-family: "RobotoCondensed";
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "EconomicaBold", sans-serif;
}

.profile {
  width: 200px;
  border-radius: 50%;
  margin: 20px;
}

.project {
  display: inline-block;
  width: 300px;
  margin: 15px 10px;
  background-color: #e3eaf2;
  padding: 1.5%;
  border-radius: 3%;
}

.project__img {
  width: 200px;
  height: 120px;
}

.socials-box {
  margin: 60px auto 30px auto;
}

.socialIcon {
  width: 50px;
  height: 50px;
  margin: 10px;
}

.investButton {
  margin: 5px 10px;
}

.toggleSkillButton {
  margin: 30px 10px;
}

.title-fade-out {
  opacity: 0;
  -webkit-animation-name: fade-out;
          animation-name: fade-out;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

.title-fade-in {
  opacity: 100;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

@-webkit-keyframes fade-out {
  from {
    opacity: 100;
  }
  to {
    opacity: 0;
  }
}

@keyframes fade-out {
  from {
    opacity: 100;
  }
  to {
    opacity: 0;
  }
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

